import React from "react";
import { Alert } from "antd";

const DiagnosisCard = ({
  type = "success",
  name,
  description = "     ",
}) => {
  name =
    type !== "success"
      ? `     .  : ${name}`
      : " !";
  return (
    <Alert message={name} description={description} type={type} showIcon />
  );
};

export default DiagnosisCard;
